import React from "react";
import { Table } from 'react-bootstrap';
import { connect } from "react-redux";

function Cart(props){
  return (
    <div>
      <div>
        <Table responsive="sm">
          <thead>
            <tr>
              <th>#</th>
              <th>상품명</th>
              <th>수량</th>
              <th>변경</th>
            </tr>
          </thead>
          <tbody>
          {
            props.goods.map( (array, index) => {
              return (   
                  <tr key={index}>
                    <td>{array.id}</td>
                    <th>{array.name}</th>
                    <th>{array.quan}</th>
                    <th>
                      <button className="m-1" onClick={ () => { props.dispatch( { type : '수량증가' } ) } }>+</button>
                      <button className="m-1" onClick={ () => { props.dispatch( { type : '수량감소'} ) } }>-</button>
                    </th>
                  </tr>
              )
            })
          }
          </tbody>
        </Table>
        {
          props.alert === true
          ? (
            <div className="my-alert">
              <p>지금 구매하시면 신규할인 20%</p>
              <button className="mt-2" onClick={ () => { props.dispatch( { type : '알림창닫기' } ) } }>닫기</button>
            </div>
            )
          : null
        }
        
      </div>
    </div>
  )
}

function storage(state){
  return {
    goods : state.reducer,
    alert : state.reducer2
  }
}

export default connect(storage)(Cart)

