/*eslint-disable*/

import React, { useContext, useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { Button, Navbar, Container, Nav} from 'react-bootstrap';
import './detail.scss';
import {supplyContext} from './App.js'
import {CSSTransition} from 'react-transition-group';
import { connect } from "react-redux";


function Detail(props){

  useEffect(function(){
    setTimeout(function(){
      alertChange(false); 
    }, 2001)
  });

  let history = useHistory();
  let { id } = useParams();
  let [alert, alertChange] = useState(true);
  let [tabSwitch, tabSwitchChange] = useState(1);
  let [animateSwitch, animateSwitchChange] = useState(false);
  let findItem = props.shoes.find(function(item){
    return item.id == id
  })

  let supplyNum = useContext(supplyContext);


  return (
    <>

      {
        alert === true
        ?
        <div className='alart-box'>
          <div className='my-alert'>
            <p>재고가 얼마 남지 않았습니다</p>
          </div>
        </div>
        : null
      }

      <div className="container">
        <div className="row">
          <div className="col-md-6">
            <img src={`https://codingapple1.github.io/shop/shoes${findItem.id + 1}.jpg`} width="100%" />
          </div>
          <div className="col-md-6 mt-4">
            <h4 className="pt-5">{findItem.title}</h4>
            <p>{findItem.content}</p>
            <p>{findItem.price}원</p>
            <Supply itemId={findItem.id}></Supply>
            <button className="btn btn-danger m-2" onClick={ () => { props.dispatch( {
              type : '항목추가', payload : { id : 2, name : '새로운 신발', quan : 1 } } )
              history.push('/cart');
              }}>주문하기</button>
            <button className="btn btn-danger m-2" onClick={ () => { history.push('/') } }>뒤로가기</button>
          </div>
        </div>
      </div> 

      <Nav className="mt-3 ms-5" variant="tabs" defaultActiveKey="link-0">
        <Nav.Item>
          <Nav.Link eventKey="link-0" onClick={ () => { animateSwitchChange(false); tabSwitchChange(1) } }>상품설명</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link-1" onClick={ () => { animateSwitchChange(false); tabSwitchChange(2) } }>배송정보</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link-2" onClick={ () => { animateSwitchChange(false); tabSwitchChange(3) } }>리뷰</Nav.Link>
        </Nav.Item>
      </Nav>

      <CSSTransition in={animateSwitch} classNames="wow" timeout={500} animateSwitchChange={animateSwitchChange}>   
        <TabContent tabSwitch={tabSwitch}></TabContent>
      </CSSTransition>
    </>
  )
}

function Supply(props) {
  let supplyNum = useContext(supplyContext);
  return (
    <p>재고 : {supplyNum[props.itemId]}</p>
  ) 
}

function TabContent(props) {

  useEffect( () => {
    props.animateSwitchChange(true);
  } )

  if( props.tabSwitch == 1 ){
    return (
      <div className='mt-4'>1번탭</div>
    )
  }else if( props.tabSwitch == 2 ){
    return (
      <div className='mt-4'>2번탭</div>
    )
  }else if( props.tabSwitch == 3){
    return (
      <div className='mt-4'>3번탭</div>
    )
  }
}


function storage(state){
  return {
    goods : state.reducer,
    alert : state.reducer2
  }
}

export default connect(storage)(Detail)