/*eslint-disable*/

import { combineReducers, createStore } from 'redux';

let goodsState = [
  { id : 0, name : '멋진신발', quan : 2 },
  { id : 1, name : '느그신발', quan : 0 },
  { id : 2, name : 'redux 너무 어려운거아니냐', quan : 100 }
];

let alertState = true;

function reducer(state = goodsState, action){
  if ( action.type === '항목추가' ){
    let copyState = [...state];
    copyState.push(action.payload);
    return copyState
  }

  if ( action.type === '수량증가' ){
    let copyState = [...state];
    copyState[0].quan++;
    return copyState
  } else if( action.type === '수량감소' ){
    let copyState = [...state];
    if ( copyState[0].quan > 0 ){
      copyState[0].quan--;
    }
    return copyState
  } else {
    return state
  }
}

function reducer2(state = alertState, action){
  if ( action.type === '알림창닫기' ){
    state = false;
    return state
  } else {
    return state
  }
}

let store = createStore(combineReducers({reducer, reducer2}));

export default store